}

.is-invalid .form-toggle__item i::after {
  background-color: #fb4143;
}

.is-invalid .form-toggle input[type="checkbox"]:checked + .form-toggle__item,
.is-invalid .form-toggle input[type="checkbox"]:checked + * .form-toggle__item,
.is-invalid .form-toggle input[type="radio"]:checked + .form-toggle__item,
